import qs from 'qs'
export default {
	data() {
		return {
			mobile: '',
			translate: 'translateY(100%)'
		}
	},
	props: {
		level: [Number,String],
		type: [Number,String],
		value: Number,
		scroll: Number
	},
	watch: {
		scroll(val) {
			if (val > 400) {
				this.translate = 'translateY(0%)'
			} else {
				this.translate = 'translateY(100%)'
			}
		}
	},
	methods: {
		paySubmit(type) {

			let myreg = /^[1][0-9]{10}$/;
			if (!myreg.test(this.mobile)) {
				console.log(myreg.test(this.mobile))
				uni.showToast({
					title: '请输入正确的手机号码',
					icon: 'none'
				});
				return;
			}
			let params = {
				app_mchid: uni.getStorageSync('token'),
				app: 1,
				order_forms: 2,
				pay_code: type,
				mobile: this.mobile,
				order_level: this.level,
				order_type: this.type,
				is_h5: 1
			}


			uni.request({
				url: this.$store.state.baseUrl+'/api/nameorder/pay',
				method: 'POST',
				data: qs.stringify(params),
				header: {
					"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
				},
				success: (res) => {
					if (res.data.code == 1) {
						location.href = res.data.data.result
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
					}

				}
			});



		}
	}
}
